<template>
  <div>
    <Card style="width: 100%; margin-bottom: 20px">
      <Alert show-icon>鼠标双击行编辑，即时修改保存</Alert>
      <Select
        v-model="tableName"
        filterable
        clearable
        placeholder="请选择表名"
        style="width: 250px; margin-right: 20px"
        @on-change="queryTableColumn"
      >
        <Option
          v-for="(item, index) in tableNameOptions"
          :key="index"
          :value="item.value"
          >{{ item.label }}</Option
        >
      </Select>

      <Select
        v-model="groupName"
        filterable
        placeholder="请选择模板组"
        style="width: 250px; margin-right: 20px"
      >
        <Option
          v-for="(item, index) in groupOptions"
          :key="index"
          :value="item"
          >{{ item }}</Option
        >
      </Select>

      <ButtonGroup>
        <Button type="primary" @click="saveOk" :disabled="!tableName"
          >字段保存</Button
        >
        <Button
          type="warning"
          @click="localGen"
          :disabled="!tableName || !groupName"
          >本地生成</Button
        >
        <Button
          type="dashed"
          :disabled="!tableName || !groupName"
          @click="download"
          >打包下载</Button
        >
      </ButtonGroup>
    </Card>
    <Table border :columns="columns" :data="data" @on-row-dblclick="updateRow">
      <template #action="{ row, index }">
        <Button
          type="warning"
          size="small"
          style="margin-right: 5px"
          @click="edit(row)"
          >编辑</Button
        >
      </template>
    </Table>

    <Drawer
      title="编辑"
      :closable="false"
      v-model="editDrawerStatus"
      placement="bottom"
      height="80"
      @on-close="editOk"
    >
      <Form :model="formItem" :label-width="180">
        <FormItem
          :label="item['title']"
          v-for="(item, index) in columns"
          :key="index"
          v-show="item['key']"
        >
          <Input v-model="formItem[item['key']]" @on-change="changeOk"></Input>
        </FormItem>
      </Form>
      <!-- <div style="float: right">
        <Button type="primary" @click="editOk">保存</Button>
      </div> -->
    </Drawer>
  </div>
</template>

<script>
import {
  tableNameList,
  tableColumnList,
  updateTableColumns,
} from "@/api/table";
import { resolveComponent } from "vue";
import { groupList } from "@/api/tpl";
import { localCodeGen, downloadCodeGen } from "@/api/code";
import { getGlobalVariable } from "@/api/global";
import FileSaver from "file-saver";
export default {
  data() {
    return {
      tableName: "",
      tableNameOptions: [],
      columns: [
        {
          title: "字段排序",
          key: "sort",
          fixed: "left",
          width: 100,
        },
        {
          title: "字段名",
          key: "columnName",
          width: 200,
        },
        {
          title: "字段类型",
          key: "columnType",
          minWidth: 100,
        },
        {
          title: "字段描述",
          key: "columnComment",
          minWidth: 100,
        },
        {
          title: "主键",
          key: "isPk",
          minWidth: 100,
        },
        {
          title: "自增",
          key: "isIncrement",
          minWidth: 100,
        },
        {
          title: "必填",
          key: "isRequired",
          minWidth: 100,
        },
        {
          title: "Java字段",
          key: "javaField",
          minWidth: 100,
        },
        {
          title: "Java类型",
          key: "javaType",
          minWidth: 100,
        },
        {
          title: "字段查询",
          key: "isQuery",
          minWidth: 100,
        },
        {
          title: "查询方式",
          key: "queryType",
          minWidth: 100,
        },
      ],
      data: [],

      tableNameOptions: [],

      groupName: "",

      tplGenFileName: "",

      editDrawerStatus: false,
      formItem: {},
    };
  },
  created() {
    this.initPage();
  },
  methods: {
    initPage() {
      this.queryTableName();
      this.getGroupName();
      this.getDynamicColumn();
    },
    queryTableName() {
      tableNameList().then((res) => {
        this.tableNameOptions = res.data.map((item) => {
          return {
            label: item.tableName + "（" + item.tableComment + "）",
            value: item.tableName,
          };
        });
      });
    },

    queryTableColumn(tableName) {
      if (!tableName) {
        this.data = [];
        return;
      }
      this.tplGenFileName = this.formatToHump(tableName);
      console.log(this.tplGenFileName);
      tableColumnList({
        tableName: tableName,
      }).then((res) => {
        this.data = res.data;
      });
    },

    getGroupName() {
      groupList().then((res) => {
        this.groupOptions = res.data;
      });
    },

    localGen() {
      localCodeGen({
        data: {
          columns: this.data,
        },

        groupName: this.groupName,
        tableName: this.tableName,
      }).then((res) => {
        if (res.code == 200) {
          this.$Message.success({
            content: res.data,
            duration: 10,
            closable: true,
          });
        }
      });
    },

    download() {
      downloadCodeGen({
        data: {
          columns: this.data,
        },

        groupName: this.groupName,
        ableName: this.tableName,
      }).then((res) => {
        if (res.code == 200) {
          window.location.href = "/api/temp/" + res.data;
        }
      });
    },

    saveOk() {
      updateTableColumns(this.tableName, this.data);
    },

    // 动态字段处理
    getDynamicColumn() {
      getGlobalVariable().then((res) => {
        let data = JSON.parse(res.data.globalJson);
        let columns = data.dynamicColumn;

        for (let c of columns) {
          console.log(c);
          this.columns.push({
            ...c,
            minWidth: 100,
          });
        }
        // this.columns.push({
        //   title: "操作",
        //   slot: "action",
        //   width: 250,
        //   align: "center",
        // });
      });
    },

    // 下划线转驼峰
    formatToHump(value) {
      console.log(value);
      return value.replace(/\_(\w)/g, (_, letter) => letter.toUpperCase());
    },

    // 字段编辑
    edit(row) {
      this.editDrawerStatus = true;
      this.formItem = { ...row };
    },

    changeOk() {
      console.log(this.formItem);
      this.data[this.formItem["_index"]] = this.formItem;

      console.log(this.data);
    },

    editOk() {
      console.log(this.formItem);
      this.data[this.formItem["_index"]] = this.formItem;
      this.editDrawerStatus = false;
      this.saveOk();
    },

    updateRow(row, index) {
      this.edit({
        ...row,
        _index: index,
      });
    },
  },
};
</script>

<style scoped lang="less">
</style>
