<template>
  <div>
    <Card style="width: 100%; margin-bottom: 20px">
      <Alert show-icon>
        全局变量管理
        <template #icon>
          <Icon type="ios-bulb-outline"></Icon>
        </template>
        <template #desc>
          1、全局变量使用JSON格式，用于模板管理、字段管理、在线生成，部分字段支持动态配置属性<br />
          2、编辑器使用monaco-editor，鼠标右键可以在线格式化JSON，或者使用快键键Shift
          + Alt + F
        </template>
      </Alert>
    </Card>

    <Button type="primary" @click="saveOk" style="margin-bottom: 10px"
      >保存</Button
    >
    <Editor
      ref="myEditor"
      :content="tplContent"
      language="json"
      @getData="getTplContentData"
      v-if="tplStatus"
    />
  </div>
</template>

<script>
import Editor from "@/components/Editor.vue";
import { getGlobalVariable, updateGlobalVariable } from "@/api/global";
export default {
  data() {
    return {
      tplContent: "",

      data: {},

      tplStatus: false,
    };
  },
  components: {
    Editor,
  },
  created() {
    this.getData();
  },
  methods: {
    getTplContentData(value) {
      console.log("=====!", value);
      this.tplContent = value;
    },

    saveOk() {
      updateGlobalVariable({
        id: this.data.id,
        globalJson: this.tplContent,
      });
    },

    getData() {
      getGlobalVariable().then((res) => {
        this.data = res.data;
        this.tplContent = res.data.globalJson;
        this.tplStatus = false;
        this.$nextTick(() => {
          this.tplStatus = true;
        });
      });
    },
  },
};
</script>

<style scoped lang="less">
</style>
