<template>
  <Row :gutter="20" style="margin-bottom: 20px">
    <Col :span="18" :offset="2">
      <Menu
        mode="horizontal"
        :theme="theme"
        :active-name="activeName"
        @on-select="changeRouter"
      >
        <MenuItem name="tpl" to="tpl">
          <Icon type="ios-paper" />
          模板管理
        </MenuItem>
        <MenuItem name="tableColumn" to="tableColumn">
          <Icon type="md-planet" />
          字段管理
        </MenuItem>
        <MenuItem name="online" to="online">
          <Icon type="md-send" />
          在线生成
        </MenuItem>
        <MenuItem name="global" to="global">
          <Icon type="md-settings" />
          全局变量
        </MenuItem>
      </Menu>
    </Col>
  </Row>

  <Row :gutter="20">
    <Col :span="18" :offset="2" style="background: #f5f5f5; padding: 10px">
      <router-view v-slot="{ Component }">
        <keep-alive v-if="$route.path !== '/tableColumn'">
          <component :is="Component" />
        </keep-alive>
        <component :is="Component" v-else />
      </router-view>
    </Col>
  </Row>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      routerKey: "1",
      activeName: "/tpl",
    };
  },
  mounted() {
    let currentPath = this.$route.path;
    this.activeName = currentPath.slice(1);
    console.log(currentPath, this.$route, this.activeName);
  },
  methods: {
    changeRouter(name) {
      this.routerKey = name;
      console.log(this.$route);
    },

    toPage(path) {
      this.$router.push(path);
    },
  },
};
</script>
<style>
body {
  overflow-x: hidden;
}
</style>
