<template>
  <Card style="width: 100%; margin-bottom: 20px">
    <Row :gutter="18">
      <Col span="12">
        <Input
          v-model="queryParam.tplName"
          placeholder="请输入模板名称查询"
          :search="true"
          @on-enter="getData"
        ></Input
      ></Col>
      <Col span="12">
        <Button type="primary" @click="add">新增模板</Button>
      </Col>
    </Row>
  </Card>
  <Table border :columns="columns" :data="data">
    <template #name="{ row }">
      <strong>{{ row.name }}</strong>
    </template>
    <template #status="{ row, index }">
      <Switch
        size="large"
        v-model="row.status"
        true-value="1"
        @on-change="changeStatus($event, row)"
      >
        <template #open>
          <span>开启</span>
        </template>
        <template #close>
          <span>关闭</span>
        </template>
      </Switch>
    </template>
    <template #action="{ row, index }">
      <Button
        type="info"
        size="small"
        style="margin-right: 5px"
        @click="$router.push('/online')"
        >查看模板</Button
      >
      <Button
        type="warning"
        size="small"
        style="margin-right: 5px"
        @click="edit(row)"
        >编辑</Button
      >
      <Button type="error" size="small" @click="remove(row.id)">删除</Button>

      <Drawer
        title="模板编辑"
        :closable="false"
        v-model="editDrawerStatus"
        placement="bottom"
        height="60"
      >
        <Form :model="formItem" :label-width="180">
          <FormItem label="模板组名">
            <Select
              v-model="formItem.groupName"
              allow-create
              @on-create="handleCreate"
              filterable
            >
              <Option
                v-for="(item, index) in groupOptions"
                :key="index"
                :value="item"
                >{{ item }}</Option
              >
            </Select>
          </FormItem>
          <FormItem label="模板名称">
            <Input v-model="formItem.tplName" placeholder="请输入模板名称">
              <template #append>
                <span>{{ randomAppend }}</span>
              </template>
            </Input>
          </FormItem>
          <FormItem label="模板类型">
            <Input
              v-model="formItem.tplType"
              placeholder="请输入模板类型"
            ></Input>
          </FormItem>
          <FormItem label="生成文件路径">
            <Input
              v-model="formItem.filePath"
              placeholder="请输入生成文件路径（本地绝对路径，支持变量）"
            ></Input>
          </FormItem>
          <FormItem label="生成文件名">
            <Input
              v-model="formItem.fileName"
              placeholder="请输入生成文件名（支持变量）"
            ></Input>
          </FormItem>
          <FormItem label="备注">
            <Input
              v-model="formItem.remark"
              type="textarea"
              :autosize="{ minRows: 2, maxRows: 5 }"
              placeholder="请输入备注"
            ></Input>
          </FormItem>
        </Form>
        <div style="float: right">
          <Button type="primary" @click="saveOk">保存</Button>
        </div>
      </Drawer>
    </template>
  </Table>
</template>
<script>
import { tplList, updateTpl, removeTpl, groupList } from "@/api/tpl";
export default {
  data() {
    return {
      columns: [
        {
          title: "模板组名",
          key: "groupName",
        },
        {
          title: "模板名称",
          key: "tplName",
        },
        {
          title: "状态",
          slot: "status",
          width: "100px",
        },
        {
          title: "模板类型",
          key: "tplType",
          width: "100px",
        },
        {
          title: "生成文件路径",
          key: "filePath",
        },
        {
          title: "生成文件名",
          key: "fileName",
        },
        {
          title: "备注",
          key: "remark",
        },
        {
          title: "创建时间",
          key: "createTime",
        },
        {
          title: "更新时间",
          key: "updateTime",
        },
        {
          title: "操作",
          slot: "action",
          width: 250,
          align: "center",
        },
      ],
      data: [],

      // 编辑
      editDrawerStatus: false,
      groupOptions: [],

      formItem: {
        groupName: "",
        tplName: "",
        tplType: "",
        remark: "",
      },

      queryParam: {},

      randomAppend: Math.random(1).toString(16).slice(2) + ".enjoy",
    };
  },
  mounted() {
    this.getData();
    this.getGroupName();
  },

  methods: {
    getGroupName() {
      groupList().then((res) => {
        this.groupOptions = res.data;
      });
    },

    handleCreate(val) {
      this.groupOptions.push(val);
    },

    getData() {
      tplList({
        ...this.queryParam,
      }).then((res) => {
        this.data = res.data;
      });
    },

    changeStatus(value, data) {
      console.log(value, data);
      let updateData = {
        ...data,
      };
      console.log(value, data);
      updateData.status = value ? 1 : 0;
      updateTpl(updateData).then((res) => {});
    },

    remove(id) {
      removeTpl({
        id: id,
      }).then((res) => {
        this.getData();
      });
    },

    edit(row) {
      this.editDrawerStatus = true;
      this.randomAppend = "." + Math.random(1).toString(16).slice(2) + ".enjoy";
      this.formItem = { ...row };
      this.formItem.tplName = this.formItem.tplName.split(".")[0];
    },

    add() {
      this.editDrawerStatus = true;
      this.formItem = {};
    },

    saveOk() {
      this.formItem.tplName = this.formItem.tplName + this.randomAppend;
      updateTpl(this.formItem).then((res) => {
        this.editDrawerStatus = false;
        this.getData();
      });
    },
  },
};
</script>
