<template>
  <div>
    <Card style="width: 100%; margin-bottom: 20px">
      <Select
        v-model="selectTpl"
        filterable
        clearable
        style="width: 300px; margin-right: 10px"
        placeholder="请选择模板"
        @on-change="changeTpl"
      >
        <Option
          v-for="item in tplOptions"
          :value="item.value"
          :key="item.value"
          >{{ item.label }}</Option
        >
      </Select>

      <Select
        v-model="tableName"
        filterable
        clearable
        style="width: 250px; margin-right: 10px"
        @on-change="queryTableColumn"
        placeholder="请选择数据库表"
      >
        <Option
          v-for="(item, index) in tableNameOptions"
          :key="index"
          :value="item.value"
          >{{ item.label }}</Option
        >
      </Select>

      <Button
        shape="circle"
        @click="
          showTpl = true;
          submit(false);
        "
        icon="ios-eye"
        v-if="!showTpl"
        style="margin-right: 10px"
        type="success"
        >预览状态</Button
      >
      <Button
        shape="circle"
        type="warning"
        @click="
          showTpl = false;
          submit(true);
        "
        icon="ios-create"
        style="margin-right: 10px"
        v-else
        >编辑状态</Button
      >

      <Button shape="circle" @click="saveTplContent" :disabled="!selectTplItem"
        >保存模板</Button
      >
      <br />
      <br />
      Enjoy 模板引擎语法：https://jfinal.com/doc/6-1
    </Card>

    <Card style="width: 100%; margin-bottom: 20px">
      <Row :gutter="16">
        <Col span="24" v-show="showTpl">
          <Editor
            ref="myEditor"
            :content="tplContent"
            @getData="getTplContentData"
            v-if="tplStatus"
          />
        </Col>

        <Col span="24" v-show="!showTpl">
          <Editor ref="myEditor2" :content="viewContent" v-if="showView" />
        </Col>
      </Row>
    </Card>
  </div>
</template>

<script>
import Editor from "@/components/Editor.vue";
import { tplList, updateTpl } from "@/api/tpl";
import { tableNameList, tableColumnList } from "@/api/table";
import { codeGen } from "@/api/code";
export default {
  data() {
    return {
      showTpl: true,
      tplContent: "",
      viewContent: "",
      showView: true,
      tplStatus: true,

      tplOptions: [],
      selectTpl: undefined,

      // select
      selectTplItem: undefined,

      // table
      tableList: [],
      tableName: "",

      tableColumnData: [],
    };
  },
  components: {
    Editor,
  },
  created() {
    this.initTplList();
    this.queryTableName();
  },
  methods: {
    saveTplContent() {
      this.selectTplItem.content = this.tplContent;
      updateTpl(this.selectTplItem);
      this.initTplList();
    },
    changeTpl(value) {
      if (!value) {
        value = { content: "" };
      }
      this.tplStatus = false;
      this.showTpl = true;
      this.tplContent = value.content;
      this.$nextTick(() => {
        this.tplStatus = true;
      });
      this.selectTplItem = value;
    },
    initTplList() {
      tplList().then((res) => {
        this.tplOptions = res.data.map((item) => {
          return {
            label: item.tplName,
            value: item,
          };
        });
      });
    },

    getTplContentData(value) {
      console.log("=====!", value);
      this.tplContent = value;
    },
    submit(value) {
      // false 为编辑  true为预览

      if (value) {
        // 预览调用接口
        this.showView = false;
        codeGen({
          content: this.tplContent,
          data: {
            columns: this.tableColumnData,
          },
        }).then((res) => {
          let data = res.data;
          this.viewContent = data;
          this.$nextTick(() => {
            this.showView = true;
          });
        });
      }
    },

    // table 相关获取
    queryTableName() {
      tableNameList().then((res) => {
        this.tableNameOptions = res.data.map((item) => {
          return {
            label: item.tableName + "（" + item.tableComment + "）",
            value: item.tableName,
          };
        });
      });
    },

    queryTableColumn(tableName) {
      console.log(tableName);
      if (!tableName) {
        this.data = [];
        return;
      }
      tableColumnList({
        tableName: tableName,
      }).then((res) => {
        this.tableColumnData = res.data;
      });
    },
  },
};
</script>

<style scoped lang="less">
</style>
