import axios from '@/utils/request'

export function tableNameList() {
  return axios.get('/table-info-list')
}

export function tableColumnList(params) {
  return axios.get('/table-columns', {
    params: {
      ...params
    }
  })
}

export function updateTableColumns(tableName, data) {
  return axios.put('/table-columns?' + 'tableName=' + tableName, data)
}