import axios from '@/utils/request'

export function tplList(data = {}) {
  return axios.post('/tpl/list', data)
}

export function updateTpl(data) {
  return axios.post('/tpl/update', data)
}


export function removeTpl(params) {
  return axios.get('/tpl/remove', {
    params: params
  })
}

export function groupList() {
  return axios.get('/tpl//group/list')
}

